<style lang="scss" scoped>
    .ui-img{
        display: inline-block;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        overflow: hidden;
        position: relative;
        // 默认图片
        background-image: url('data:image/svg+xml;base64,');
        .ui-img-tools{
            // 图片中的标题
            text-overflow: ellipsis;
            white-space: normal;
            word-break: break-all;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            // 图片中的标题 end
            overflow: hidden;
            line-height: normal;
            position: absolute;
            bottom: 0;
            left: 0;
            background-color: rgba(black, 0.3);
            padding:.2rem .3rem;
            margin: 0;
            font-size: .6rem;
            color: #fff;
            width: 100%;
            text-align: left;
        }
    }
    .weight-full{
        width: 100%;
    }
    .height-full{
        height: 100%;
    }
</style>
<template>
    <div class="ui-img" :style="{ backgroundImage: imgSrc, width:formatSize, height:formatSize, borderRadius:radius }" >
        <!--标题-->
        <div v-if="$slots.default" class="ui-img-tools">
            <slot></slot>
        </div>
    </div>
</template>
<script>
    export default {
        name:'ui-img',
        props:{
            src:{
                type: String,
                require: true
            },
            size:{
                type: String,
                default: ''
            },
            circle:{
                type:Boolean,
                default:false
            }
        },
        data(){
            return {
                imgWeightFull:false,
                imgHeightFull:false,
            }
        },
        computed: {
            imgSrc(){
                if (this.src) {
                    return `url(${encodeURI(this.src)})`
                }
                return ''
            },
            formatSize(){
                if (this.size.indexOf('rem')>-1 || this.size.indexOf('px')>-1) {
                    return this.size
                }
                return this.size + 'px'
            },
            radius(){
                return this.circle?'50%':''
            }
        }
    }
</script>